*{
  margin: 0;
  padding: 0;
}

body{
  background-image: url(https://www.rawpixel.com/image/11909790/gradient-pink-aesthetic-iphone-wallpaper-background#eyJrZXlzIjoiYmFja2dyb3VuZCIsInNvcnRlZEtleXMiOiJiYWNrZ3JvdW5kIn0=);
  font-family:'Times New Roman', Times, serif;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

h1{
  color: brown;
  text-align: center;
}
.app{
    
  background:linear-gradient(5deg, #ff961d 0%, #f71aa2 100%);
  width: 95%;
  min-height: 100%;
  height: max-content;
  border-radius: 16px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0, 75);
   display: flex;
   justify-content: space-evenly;

}
.Question-section{
  width: 100%;
  position: relative;
}

.Question-count{
  margin-bottom: 20px;

}
.Question-count span{
  font-size: 28px;

}

.Question-text{
  margin-bottom: 12px;
  color: white;
  font-size: 30px;
}

.Answer-section{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  
}
button{
  width: 100%;
  font-size: 16px;
  color: #ffffff;
  background: linear-gradient(#ff961d 0%, #f71aa2 100%);
  border-radius: 15px;
  display: flex;
  padding: 10px;
  justify-content: flex-start;
  align-items: center;
  border: 1px solid #234668;
    cursor: pointer;
    margin-top: 10px;

}